import React from 'react'
import { Container, FormControl, Heading, HStack, Input, Textarea, VStack,Text, Spacer } from "@chakra-ui/react"

const Contact = () => {
  return (
    <Container minW="80%" minH="100vh">
      <Heading>Contact Me</Heading>
      <HStack mt={5}>
        <VStack gap={5} align="flex-start">
          <Heading size="md">Linked In</Heading>
          <Text>atuls289549</Text>
          <Heading size="md">GitHub</Heading>
          <Text>atuls2060</Text>
          <Heading size="md">Call Me</Heading>
          <Text>+91 8839652312</Text>
        </VStack>
        <Spacer />
        <FormControl maxW="50%">
          <HStack mb={5} gap={2}><Input type="text" placeholder='Your Name' />
            <Input type="email" placeholder='Your Name' /></HStack>
          <Textarea mb={5} placeholder="Message" />
          <Input type="submit" value="Send" backgroundColor="brand" color="white" />
        </FormControl>
      </HStack>
    </Container>
  )
}

export default Contact